module.exports = "\n<i-article>\n    <article>\n        <h1>LoadingBar 加载进度条</h1>\n        <Anchor title=\"概述\" h2></Anchor>\n        <p>全局创建一个显示页面加载、异步请求、文件上传等的加载进度条。</p>\n        <Anchor title=\"说明\" h2></Anchor>\n        <p>LoadingBar 只会在全局创建一个，因此在任何位置调用的方法都会控制这同一个组件。主要使用场景是路由切换和Ajax，因为这两者都不能拿到精确的进度，LoadingBar 会模拟进度，当然也可以通过<code>update()</code>方法来传入一个精确的进度，比如在文件上传时会很有用，具体见API。</p>\n        <Anchor title=\"在路由中使用\" h4></Anchor>\n        <i-code bg>{{ code.router }}</i-code>\n        <Anchor title=\"在异步请求中使用\" h4></Anchor>\n        <i-code bg lang=\"html\">{{ code.ajax }}</i-code>\n        <Anchor title=\"代码示例\" h2></Anchor>\n        <Demo title=\"基本用法\">\n            <div slot=\"demo\">\n                <btn on-select=\"start\">Start</btn>\n                <btn on-select=\"finish\">Finish</btn>\n                <btn on-select=\"error\">Error</btn>\n            </div>\n            <div slot=\"desc\">\n                <p>点击 Start 开始进度，点击 Finish 结束。在调用<code>start()</code>方法后，组件会自动模拟进度，当调用<code>finish()</code>或<code>error()</code>时，补全进度并自动消失。</p>\n            </div>\n            <i-code lang=\"html\" slot=\"code\">{{ code.base }}</i-code>\n        </Demo>\n        <div class=\"api\">\n            <Anchor title=\"API\" h2></Anchor>\n            <Anchor title=\"LoadingBar instance\" h3></Anchor>\n            <p>通过直接调用以下方法来使用组件：</p>\n            <ul>\n                <li>\n                    <code>LoadingBar.start()</code>\n                </li>\n                <li>\n                    <code>LoadingBar.finish()</code>\n                </li>\n                <li>\n                    <code>LoadingBar.error()</code>\n                </li>\n                <li>\n                    <code>LoadingBar.update(percent)</code>\n                </li>\n            </ul>\n            <p>以上方法隐式的创建及维护San组件。函数及参数说明如下：</p>\n            <table>\n                <thead>\n                    <tr>\n                        <th>函数名</th>\n                        <th>说明</th>\n                        <th>参数</th>\n                    </tr>\n                </thead>\n                <tbody>\n                    <tr>\n                        <td>start</td>\n                        <td>开始从 0 显示进度条，并自动加载进度</td>\n                        <td>无</td>\n                    </tr>\n                    <tr>\n                        <td>finish</td>\n                        <td>结束进度条，自动补全剩余进度</td>\n                        <td>无</td>\n                    </tr>\n                    <tr>\n                        <td>error</td>\n                        <td>以错误的类型结束进度条，自动补全剩余进度</td>\n                        <td>无</td>\n                    </tr>\n                    <tr>\n                        <td>update</td>\n                        <td>精确加载到指定的进度</td>\n                        <td>percent，指定的进度百分比</td>\n                    </tr>\n                </tbody>\n            </table>\n            <p>另外提供了全局配置和全局销毁的方法：</p>\n            <ul>\n                <li>\n                    <code>LoadingBar.config(options)</code>\n                </li>\n                <li>\n                    <code>LoadingBar.destroy()</code>\n                </li>\n            </ul>\n            <br>\n            <i-code bg>{{ code.config }}</i-code>\n            <br>\n            <table>\n                <thead>\n                    <tr>\n                        <th>属性</th>\n                        <th>说明</th>\n                        <th>类型</th>\n                        <th>默认值</th>\n                    </tr>\n                </thead>\n                <tbody>\n                    <tr>\n                        <td>color</td>\n                        <td>进度条的颜色，默认为 iView 主色</td>\n                        <td>String</td>\n                        <td>primary</td>\n                    </tr>\n                    <tr>\n                        <td>failedColor</td>\n                        <td>失败时的进度条颜色，默认为 iView 主色</td>\n                        <td>String</td>\n                        <td>error</td>\n                    </tr>\n                    <tr>\n                        <td>height</td>\n                        <td>进度条高度，单位 px</td>\n                        <td>Number</td>\n                        <td>2</td>\n                    </tr>\n                </tbody>\n            </table>\n        </div>\n    </article>\n</i-article>\n";

