module.exports = "\n<div>\n    <article>\n        <h1>Menu 导航菜单</h1>\n        <anchor title=\"概述\" h2></anchor>\n        <p>为页面和功能提供导航的菜单列表，常用于网站顶部和左侧。</p>\n        <anchor title=\"代码示例\" h2></anchor>\n        <demo title=\"顶部导航\">\n            <div slot=\"demo\">\n                <menu mode=\"horizontal\" theme=\"{{theme1}}\" activeName=\"1\">\n                    <menu-item name=\"1\">\n                        <Icon type=\"ios-paper\"></Icon>\n                        内容管理\n                    </menu-item>\n                    <menu-item name=\"2\">\n                        <Icon type=\"ios-people\"></Icon>\n                        用户管理\n                    </menu-item>\n                    <submenu name=\"3\">\n                        <span slot=\"title\">\n                            <Icon type=\"stats-bars\"></Icon>\n                            统计分析\n                        </span>\n                        <menu-group title=\"使用\">\n                            <menu-item name=\"3-1\">新增和启动</menu-item>\n                            <menu-item name=\"3-2\">活跃分析</menu-item>\n                            <menu-item name=\"3-3\">时段分析</menu-item>\n                        </menu-group>\n                        <menu-group title=\"留存\">\n                            <menu-item name=\"3-4\">用户留存</menu-item>\n                            <menu-item name=\"3-5\">流失用户</menu-item>\n                        </menu-group>\n                    </submenu>\n                </menu>\n                <br>\n                <p>切换主题</p>\n                <radio-group label=\"{=theme1=}\">\n                    <radio label=\"light\"></radio>\n                    <radio label=\"dark\"></radio>\n                    <radio label=\"primary\"></radio>\n                </radio-group>\n            </div>\n            <div slot=\"desc\">\n                <p>水平的顶部导航菜单。</p>\n                <p>通过设置属性 <code>theme</code> 为 <code>light</code>、<code>dark</code>、<code>primary</code> 可以选择主题。</p>\n                <p>通过事件 <code>on-select</code> 可以得到点击菜单的 name 值，从而控制页面路由或自定义操作。</p>\n            </div>\n            <i-code lang=\"html\" slot=\"code\">{{ code.base }}</i-code>\n        </demo>\n        <demo title=\"顶部导航\">\n            <div slot=\"demo\">\n                <menu mode=\"vertical\" theme=\"{{theme2}}\">\n                    <menu-group title=\"文章管理\">\n                        <menu-item name=\"1-1\">文章管理</menu-item>\n                        <menu-item name=\"1-2\">评论管理</menu-item>\n                        <menu-item name=\"1-3\">举报管理</menu-item>\n                    </menu-group>\n                    <submenu name=\"2\">\n                        <span slot=\"title\">\n                            <Icon type=\"ios-people\"></Icon>\n                            用户管理\n                        </span>\n                        <menu-item name=\"2-1\">新增用户</menu-item>\n                        <menu-item name=\"2-2\">活跃用户</menu-item>\n                    </submenu>\n                    <submenu name=\"3\">\n                        <span slot=\"title\">\n                            <Icon type=\"stats-bars\"></Icon>\n                            统计分析\n                        </span>\n                        <menu-group title=\"使用\">\n                            <menu-item name=\"3-1\">新增和启动</menu-item>\n                            <menu-item name=\"3-2\">活跃分析</menu-item>\n                            <menu-item name=\"3-3\">时段分析</menu-item>\n                        </menu-group>\n                        <menu-group title=\"留存\">\n                            <menu-item name=\"3-4\">用户留存</menu-item>\n                            <menu-item name=\"3-5\">流失用户</menu-item>\n                        </menu-group>\n                    </submenu>\n                </menu>\n            </div>\n            <div slot=\"desc\">\n                <p>水平的顶部导航菜单。</p>\n                <p>通过设置属性 <code>theme</code> 为 <code>light</code>、<code>dark</code>、<code>primary</code> 可以选择主题。</p>\n                <p>通过事件 <code>on-select</code> 可以得到点击菜单的 name 值，从而控制页面路由或自定义操作。</p>\n            </div>\n            <i-code lang=\"html\" slot=\"code\">{{ code.vertical }}</i-code>\n        </demo>\n        <div class=\"api\">\n            <anchor title=\"API\" h2></anchor>\n            <anchor title=\"Menu props\" h3></anchor>\n            <table>\n                <thead>\n                    <tr>\n                        <th>属性</th>\n                        <th>说明</th>\n                        <th>类型</th>\n                        <th>默认值</th>\n                    </tr>\n                </thead>\n                <tbody>\n                    <tr>\n                        <td>mode</td>\n                        <td>菜单类型，可选值为 <code>horizontal</code>（水平） 和 <code>vertical</code>（垂直）</td>\n                        <td>String</td>\n                        <td>vertical</td>\n                    </tr>\n                    <tr>\n                        <td>theme</td>\n                        <td>主题，可选值为 <code>light</code>、<code>dark</code>、<code>primary</code>，其中 primary 只适用于 <code>mode=\"horizontal\"</code></td>\n                        <td>String</td>\n                        <td>light</td>\n                    </tr>\n                    <tr>\n                        <td>activeName</td>\n                        <td>激活菜单的 name 值</td>\n                        <td>String | Number</td>\n                        <td>-</td>\n                    </tr>\n                    <tr>\n                        <td>accordion</td>\n                        <td>是否开启手风琴模式，开启后每次至多展开一个子菜单</td>\n                        <td>Boolean</td>\n                        <td>false</td>\n                    </tr>\n                    <tr>\n                        <td>width</td>\n                        <td>导航菜单的宽度，只在 <code>mode=\"vertical\"</code> 时有效，如果使用 <code>i-col</code> 等布局，建议设置为 <code>auto</code></td>\n                        <td>String</td>\n                        <td>240px</td>\n                    </tr>\n                </tbody>\n            </table>\n            <anchor title=\"Menu events\" h3></anchor>\n            <table>\n                <thead>\n                    <tr>\n                        <th>事件名</th>\n                        <th>说明</th>\n                        <th>返回值</th>\n                    </tr>\n                </thead>\n                <tbody>\n                    <tr>\n                        <td>on-select</td>\n                        <td>选择菜单（MenuItem）时触发</td>\n                        <td>name</td>\n                    </tr>\n                </tbody>\n            </table>\n            <anchor title=\"MenuItem props\" h3></anchor>\n            <table>\n                <thead>\n                    <tr>\n                        <th>属性</th>\n                        <th>说明</th>\n                        <th>类型</th>\n                        <th>默认值</th>\n                    </tr>\n                </thead>\n                <tbody>\n                    <tr>\n                        <td>name</td>\n                        <td>菜单项的唯一标识，必填</td>\n                        <td>String | Number</td>\n                        <td>-</td>\n                    </tr>\n                </tbody>\n            </table>\n            <anchor title=\"MenuGroup props\" h3></anchor>\n            <table>\n                <thead>\n                    <tr>\n                        <th>属性</th>\n                        <th>说明</th>\n                        <th>类型</th>\n                        <th>默认值</th>\n                    </tr>\n                </thead>\n                <tbody>\n                    <tr>\n                        <td>title</td>\n                        <td>分组标题</td>\n                        <td>String</td>\n                        <td>空</td>\n                    </tr>\n                </tbody>\n            </table>\n        </div>\n    </article>\n</div>\n";

